<style>
    .listing-table .dropdown-menu-right {
        right: 40px !important;
    }
    .listing-table .dropdown-menu {
        top: -60px !important;
    }
</style>
<template>
    <div class="card shadow"
         :class="type === 'dark' ? 'bg-default': ''" v-if="$can(permissionPrefix + '.view')">
        <div class="card-header border-0"
             :class="type === 'dark' ? 'bg-transparent': ''">
            <div class="row align-items-center">
                <div class="col-lg-4 col-xs-12 col-sm-12 col-md-12">
                    <h3 class="mb-0" :class="type === 'dark' ? 'text-white': ''">
                        {{tableTitle}}
                        <small>(Total - {{ total_rows }})</small>
                    </h3>
                </div>
                <div class="col-lg-4 col-xs-12 col-sm-6 col-md-6 text-right width-50-mobile" v-if="$can(permissionPrefix + '.view')">
                    <base-input inputClasses="mb-0" class="mb-0" :block="false" :placeholder="searchCriteria ? searchCriteria :'Search'"
                                v-model="searchable" v-on:keyup="searchResults"/>
                </div>
                <div class="col-lg-4 col-xs-12 col-sm-6 col-md-6 text-right width-50-mobile">
                    <base-button v-if="$can(permissionPrefix + '.create')"
                                 :block="false" :native-type="'button'" size="'small" type="primary"
                                 @click="create">Add New
                    </base-button>
                </div>
            </div>
        </div>

        <div class="table-responsive listing-table">
            <base-table class="table align-items-center table-flush table-condesed table-hovered table-striped"
                        :class="type === 'dark' ? 'table-dark': ''"
                        :thead-classes="type === 'dark' ? 'thead-dark': 'thead-light'"
                        tbody-classes="list"
                        :data="laravelData.data">
                <template slot="columns">
                    <th v-for="key in tableValues" :key="'th' + key">{{ key.toUpperCase() }}</th>
                    <!--<th>Permission name</th>
                    <th>Permission For</th>
                    <th>Creation Time</th>
                    <th>Last Update Time</th>-->
                    <th></th>
                </template>

                <template slot-scope="{row}">
                    <!--<th scope="row">
                      <div class="media align-items-center">
                        <div class="media-body">
                          <span class="name mb-0 text-sm">{{row.id}}</span>
                        </div>
                      </div>
                    </th>-->
                    <td class="budget" v-for="tv in tableKeys" :key="'td' + tv">
                        <span v-if="typeof(row[tv]) == 'number'">{{ row[tv] }} </span>
                        <span v-if="typeof(row[tv]) == 'string'">{{ row[tv] }} </span>
                        <div v-if="row[tv] != null && typeof(row[tv]) !== 'undefined' && typeof(row[tv]) == 'object'">
                            <!--<p class="badge badge-pill badge-dark" v-for="r in row[tv].map(x => x.name)" :key="r">
                                {{ r | capitalize }}
                            </p>
                            <base-button type="primary"  v-for="r in row[tv].map(x => x.name)" :key="r">
                                <span>{{ r | capitalize }}</span>
                            </base-button>-->
                            <div v-if="row[tv].length == 1">
                                <base-button size="sm" :block="true" type="primary"
                                             v-for="r in row[tv].map(x => { return x.name || x.title} )"
                                             :key="r">
                                    <span>{{ r }}</span>
                                </base-button>
                            </div>
                            <div v-if="row[tv].length > 1">  
                                <base-button size='sm' :block="true" type="primary" @click="showDataInModal(row[tv].map(x => x.name))">
                                    Click to View
                                </base-button>
                            </div>
                            <div v-if="row[tv].name">
                                <base-button size="sm" :block="true" type="primary">
                                    <span>{{ row[tv].name }}</span>
                                </base-button>
                            </div>
                            <div v-if="row[tv].title">
                                <base-button size="sm" :block="true" type="primary">
                                    <span>{{ row[tv].title }}</span>
                                </base-button>
                            </div>
                        </div>

                    </td>
                    <!--<td class="budget">
                      {{row.guard_name}}
                    </td>
                    <td>
                      {{ row.created_at}}
                    </td>
                    <td>
                      {{ row.updated_at}}
                    </td>-->

                    <td class="text-right">
                        <base-dropdown class="dropdown"
                                       position="right">
                            <a slot="title" class="btn btn-sm btn-icon-only text-light" role="button"
                               data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i class="fas fa-ellipsis-v"></i>
                            </a>

                            <template>
                                <a v-if="$can(permissionPrefix + '.edit') || $can(permissionPrefix + '.update')"
                                   class="dropdown-item" href="javascript:void(0)" v-on:click="edit(row)">Edit</a>

                                <a v-if="$can(permissionPrefix + '.delete')" class="dropdown-item"
                                   href="javascript:void(0)"
                                   v-on:click="deleteAction(row)">Delete</a>
                            </template>
                        </base-dropdown>
                    </td>

                </template>

            </base-table>

            <!--      <ul>-->
            <!--        <li v-for="post in laravelData.data" :key="post.id">{{ post.trx_id }}</li>-->
            <!--      </ul>-->
        </div>

        <div class="card-footer d-flex justify-content-end"
             :class="type === 'dark' ? 'bg-transparent': ''">
            <!--      <base-pagination total="30"></base-pagination>-->
            <pagination :limit="2" :align="'right'" :size="'small'" :data="laravelData"
                        @pagination-change-page="getResults"></pagination>
        </div>


        <modal :type="''" :show.sync="modals.modal1"
               v-if="$can(permissionPrefix + '.create') || $can(permissionPrefix + '.edit') || $can(permissionPrefix + '.update')"
               body-classes="p-0"
               modal-classes="modal-dialog-centered modal-sm">
            <card type="secondary" shadow
                  header-classes="bg-white pb-5"
                  body-classes="px-lg-5 py-lg-5"
                  class="border-0">
                <template>
                    <form role="form" @submit.prevent>
                        <div v-for="(formElem, i) in formProps" :key="'elem' + i">
                            <div v-if="formElem.type === 'text'">
                                <base-input :disabled="formElem.disableUpdate && editMode"
                                            :label="formElem.label" alternative
                                            class="mb-3" :type="formElem.nativeType != null ? formElem.nativeType : ''"
                                            :placeholder="formElem.label" v-model="selectedObject[formElem.key]">
                                </base-input>
                            </div>

                            <div v-if="formElem['type'] === 'select'">
                                <div class="form-group">
                                    <label>{{ formElem.label }}</label>
                                    <select class="form-control" v-model="selectedObject[formElem.key]"
                                            :disabled="formElem.disableUpdate && editMode"
                                            v-on:change="handleChangeFunction(formElem)">
                                        <option v-for="(option, prop) in formElem.options" :key="'op' + prop"
                                                :value="prop"
                                                :selected="selectedObject[formElem.key] === prop">
                                            {{ option }}
                                        </option>
                                    </select>
                                </div>
                            </div>

                            <div v-if="formElem['type'] === 'select-single'">
                                <div class="form-group">
                                    <label>{{ formElem.label }}</label>

                                    <multiselect v-model="selectedObject[formElem.key]" id="select-single" label="name"
                                                 :disabled="formElem.disableUpdate && editMode"
                                                 track-by="id"
                                                 placeholder="Select an option" open-direction="bottom"
                                                 :options="formElem.options"
                                                 :loading="isLoading" :max-height="600" :show-no-results="true"
                                                 @open="getOptions(formElem)">
                                        <span slot="noResult">Oops! No elements found. Consider changing the search query.</span>
                                    </multiselect>
                                </div>
                            </div>

                            <div v-if="formElem['type'] === 'select-multiple'">
                                <div class="form-group">
                                    <label>{{ formElem.label }}</label>

                                    <multiselect v-model="selectedObject[formElem.key]" id="ajax" label="name"
                                                 :disabled="formElem.disableUpdate && editMode"
                                                 track-by="id"
                                                 placeholder="Type to search" open-direction="bottom"
                                                 :options="formElem.options" :multiple="true" :searchable="true"
                                                 :loading="isLoading" :internal-search="true" :clear-on-select="false"
                                                 :close-on-select="false" :max-height="600" :show-no-results="false"
                                                 :hide-selected="true" @search-change="getOptions(formElem)"
                                                 @open="getOptions(formElem)">

                                        <template slot="tag" slot-scope="{ option, remove }"><span
                                                class="multiselect__tag"><span>{{ option.name }}</span><i
                                                class="multiselect__tag-icon" @click="remove(option)"></i></span>
                                        </template>
                                        <template slot="clear" slot-scope="props">
                                            <div class="multiselect__clear"
                                                 @mousedown.prevent.stop="clearAll(props.search)"></div>
                                        </template>
                                        <span slot="noResult">Oops! No elements found. Consider changing the search query.</span>
                                    </multiselect>
                                </div>
                            </div>
                        </div>
                        <div class="text-center">
                            <base-button type="primary" class="my-4" @click="createOrUpdate">
                                {{ !editMode ? 'Create' : 'Update' }}
                            </base-button>
                        </div>
                    </form>
                </template>
            </card>
        </modal>



        <modal :type="''" :show.sync="modals.listModal"
               body-classes="p-0"
               modal-classes="modal-dialog-centered modal-sm">
            <card type="secondary" shadow
                  header-classes="bg-white pb-5"
                  body-classes="px-lg-5 py-lg-5"
                  class="border-0">
                <template>
                    <div class="table-responsive">
                        <table class="table table-bordered table-stripped table-hovered table-condensed">
                            <tr v-for="data in modalListData" :key="data">
                                <td style="font-weight: bold" >{{ data }}</td>
                            </tr>
                        </table>
                    </div>
                </template>
            </card>
        </modal>

    </div>
</template>
<script>
    import Vue from 'vue';
    import Multiselect from 'vue-multiselect';
    import helpers from "../../../utils/helpers";

    export default {
        name: 'listing-table',
        components: {Multiselect},
        mixins: [helpers],
        props: {
            type: {
                type: String
            },
            tableHeaders: Object,
            apiMethod: Object,
            title: String,
            service: Object,
            createOrEditForm: Array,
            permissionPrefix: String,
            searchCriteria: String
        },
        data() {
            return {
                // Our data object that holds the Laravel paginator data
                laravelData: {},
                searchable: '',
                tableTitle: this.title,
                total_rows: 0,
                tableKeys: Object.keys(this.tableHeaders),
                tableValues: Object.values(this.tableHeaders),
                formProps: this.createOrEditForm,
                modals: {
                    modal1: null,
                    listModal: null
                },
                selectedObject: {},
                editMode: false,
                isLoading: false,
                typingTimer: Number,
                doneTypingTimer: 1000,
                modalListData: []
            }
        },

        created() {
            // Fetch initial results
            this.getResults();
        },

        methods: {
            // Our method to GET results from a Laravel endpoint
            getResults(p = 1) {
                this.service[this.apiMethod.list](p, (this.searchable !== undefined ? this.searchable : '')).then((response) => {
                    this.laravelData = response.data;
                    this.total_rows = response.data.total;
                }).catch((error) => {
                    window.EventBus.$emit('error', {
                        message: this.$showError(error),
                    });
                });
            },
            searchResults() {
                clearTimeout(this.typingTimer);
                this.typingTimer = setTimeout(this.getResults, this.doneTypingTimer);
            },
            edit(row) {
                if (row.roles && row.roles.length > 0) {
                    row.role = JSON.parse(JSON.stringify(row.roles[0]));
                }

                this.editMode = true;
                this.selectedObject = row;
                this.modals.modal1 = true;
                window.console.log(row);
                this.getOptions();
            },
            create() {
                this.editMode = false;
                this.selectedObject = {};
                this.modals.modal1 = true;
            },
            createOrUpdate() {
                this.service[this.apiMethod.entry](this.selectedObject).then((response) => {
                    this.modals.modal1 = false;
                    window.console.log(response);
                    if (response.status === 201) {
                        this.laravelData.data.push(response.data);
                        this.total_rows = this.total_rows + 1;

                        window.EventBus.$emit('success', {
                            message: 'Information has been created successfully',
                        });
                        this.getResults();
                    } else {
                        let index = this.laravelData.data.findIndex((x) => {
                            window.console.log(x.id, response.data.id);
                            return x.id === response.data.id;
                        });
                        if (index >= 0) {
                            this.laravelData.data[index] = response.data;
                        }
                        window.EventBus.$emit('success', {
                            message: 'Information has been updated successfully',
                        });
                    }
                }).catch((error) => {
                    window.EventBus.$emit('error', {
                        message: this.$showError(error),
                    });
                });
            },
            deleteAction(row) {
                Vue.swal({
                    showLoaderOnConfirm: false,
                    title: 'Are you sure!',
                    text: 'Do you want to delete',
                    icon: 'question',
                    confirmButtonText: 'Yes, I am sure',
                    showCancelButton: true
                }).then((resp) => {
                    if (resp.isConfirmed) {
                        this.service[this.apiMethod.delete](row).then(() => {

                            /*this.laravelData.splice(this.laravelData.findIndex(function(i){
                                window.console.log(i.id, row.id);
                                return i.id === row.id;
                            }), 1);*/

                            this.laravelData.data = this.laravelData.data.filter(function (el) {
                                return el.id !== row.id;
                            });

                            window.EventBus.$emit('success', {
                                message: 'Deleted!',
                            });
                        }).catch((error) => {
                            window.EventBus.$emit('error', {
                                message: this.$showError(error),
                            });
                        });
                    }
                });
            },
            getOptions(elem) {
                let dependentElem = {};
                let dependOnElem = {};
                if (elem) {
                    if (elem.options !== undefined && elem.options.length > 0) {
                        return elem.options;
                    } else {
                        // If has dependent
                        if (elem.dependent !== undefined) {
                            if (elem.key !== undefined) {
                                dependentElem = this.createOrEditForm.filter(x => x.id === elem.dependent)[0];
                            }
                        } else {
                            dependentElem = elem;
                        }

                        // If data is dependent on
                        if (elem.dependOn !== undefined) {
                            dependOnElem = this.createOrEditForm.filter(x => x.id === elem.dependOn)[0];
                        }

                        if (typeof (dependentElem.apiMethod) !== undefined) {

                            let key = null;
                            if (dependOnElem && Object.keys(dependOnElem).length > 0) {
                                key = this.selectedObject[dependOnElem.key]
                            } else {
                                key = this.selectedObject[elem.key];
                            }

                            this.isLoading = true;
                            this.service[dependentElem.apiMethod](key).then((resp) => {
                                this.isLoading = false;
                                let list = [];
                                resp.data.forEach(item => {
                                    if(item.name)
                                        list.push({id: item.id, name: item.name});

                                    if(item.title)
                                        list.push({id: item.id, name: item.title})
                                });
                                dependentElem.options = list;
                            }).catch(() => {
                                // window.console.log(this.showError(error));
                            });
                        }
                    }
                }
                // return [{value: 1, text: 'Item 1'}, {value: 2, text: 'Item 2'}];
            },
            limitText(count) {
                return `and ${count} other countries`
            },
            clearAll(model) {
                window.console.log(model);
                return model = [];
            },
            handleChangeFunction(elem) {
                if (elem.onChangeOptions !== undefined) {
                    this[elem.onChangeOptions](elem);
                }
            },
            showDataInModal(data) {
                this.modalListData = data;
                this.modals.listModal = true;

            }
            /*showError(error) {
                if (typeof (error) == 'string') {
                    return error;
                } else if (typeof (error) == 'object') {

                    if (typeof (error.response.data.message) !== 'undefined') {
                        return error.response.data.message;
                    } else if (typeof (error.message) !== 'undefined') {
                        return error.message;
                    } else if (typeof (error.error) !== 'undefined') {
                        return error.error[0];
                    } else {
                        return error;
                    }
                } else {
                    return error;
                }
            }*/
        },
        filters: {
            capitalize: function (value) {
                if (!value) return '';
                value = value.toString();
                return value.charAt(0).toUpperCase() + value.slice(1)
            }
        }
    }
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
