<template>
    <div class="card shadow"
         :class="type === 'dark' ? 'bg-default': ''">
        <div class="card-header border-0"
             :class="type === 'dark' ? 'bg-transparent': ''">
            <div class="row align-items-center">
                <div class="col">
                    <h3 class="mb-0" :class="type === 'dark' ? 'text-white': ''">
                        {{tableTitle}} ({{ total_rows }})
                    </h3>
                </div>
                <div class="col text-right" v-if="$can(permissionPrefix + '.view')">
                    <base-input placeholder="Transaction ID or Customer account or Merchant Shortcode" v-model="searchable" v-on:keyup="searchResults"/>
                    <!--          <base-button type="primary" size="sm">See all</base-button>-->
                </div>
            </div>
        </div>

        <div class="table-responsive">
            <base-table class="table align-items-center table-flush"
                        v-if="$can(permissionPrefix + '.view')"
                        :class="type === 'dark' ? 'table-dark': ''"
                        :thead-classes="type === 'dark' ? 'thead-dark': 'thead-light'"
                        tbody-classes="list"
                        :data="laravelData.data">
                <template slot="columns">
                    <th>Transaction ID</th>
                    <th>Customer Account</th>
                    <th>Merchant Name</th>
                    <th>Merchant Shortcode</th>
                    <th>Amount</th>
                    <th>Status</th>
                    <th>Transaction Time</th>
                    <th>User Reference</th>
                    <th>Merchant Reference</th>
                    <th>Ref. Added By</th>
<!--                    <th></th>-->
                </template>

                <template slot-scope="{row}">
                    <th scope="row">
                        <div class="media align-items-center">
                            <div class="media-body">
                                <span class="name mb-0 text-sm">{{row.trx_id}}</span>
                            </div>
                        </div>
                    </th>
                    <td class="budget">
                        {{row.sender_account_no}}
                    </td>
                    <td class="budget">
                        {{row.merchant_details ? row.merchant_details.name : ''}} 
                    </td>
                    <td class="budget">
                        {{ row.merchant_details ? row.merchant_details.account_no : '' }}
                    </td>
                    <td class="budget">
                        {{row.amount}} BDT
                    </td>
                    <td>
                        <badge class="badge-dot mr-4" :type="'success'">
                            <i :class="`bg-success`"></i>
                            <span class="status">PAID</span>
                        </badge>
                    </td>
                    <td>
                        {{ row.transaction_datetime}}
                    </td>
                    <td>
                        {{ row.transactionReference}}
                    </td>
                    <td>
                        {{ row.merchant_ref}}
                    </td>
                    <td>
                        {{ row.reference_added_by ? row.reference_added_by.username : '' }}
                    </td>

                    <!--<td class="text-right">
                        <base-dropdown class="dropdown"
                                       position="right">
                            <a slot="title" class="btn btn-sm btn-icon-only text-light" role="button"
                               data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i class="fas fa-ellipsis-v"></i>
                            </a>

                            <template>
                                <a class="dropdown-item" href="#">Action</a>
                                <a class="dropdown-item" href="#">Another action</a>
                                <a class="dropdown-item" href="#">Something else here</a>
                            </template>
                        </base-dropdown>
                    </td>-->

                </template>

            </base-table>

            <!--      <ul>-->
            <!--        <li v-for="post in laravelData.data" :key="post.id">{{ post.trx_id }}</li>-->
            <!--      </ul>-->
        </div>

        <div class="card-footer d-flex justify-content-end"
             :class="type === 'dark' ? 'bg-transparent': ''">
            <!--      <base-pagination total="30"></base-pagination>-->
            <pagination :limit="2" :align="'right'" :size="'small'" :data="laravelData"
                        @pagination-change-page="getResultsfrompagination"></pagination>
        </div>

    </div>
</template>
<script>
    import paymentService from "../../store/modules/payment";
    import helpers from "../../utils/helpers";

    export default {
        name: 'all-payments-table',
        mixins: [helpers],
        props: {
            type: {
                type: String
            },
            searchable: String,
            permissionPrefix: String
        },
        data() {
            return {
                // Our data object that holds the Laravel paginator data
                laravelData: {},
                search: this.searchable,
                tableTitle: "All Payments",
                total_rows: 0,
                typingTimer: Number,
                doneTypingTimer: 1000,
            }
        },

        mounted() {
            // Fetch initial results
            this.getResultsfrompagination();
        },

        methods: {
            // Our method to GET results from a Laravel endpoint
            getResultsfrompagination(page = 1) {
                clearTimeout(checkFinishTyping);
                let that = this;

                let checkFinishTyping = setTimeout(function(){
                    paymentService.getAllPayments(page, (that.$props.searchable !== undefined ? that.$props.searchable : '')).then((response) => {
                        that.laravelData = response.data;
                        that.total_rows = response.data.total;
                    }).catch((error) => {
                        window.EventBus.$emit('error', {
                            message: that.$showError(error),
                        });
                    });
                }, 3000)
            },
            
            searchResults() {
                clearTimeout(this.typingTimer);
                this.typingTimer = setTimeout(this.getResults, this.doneTypingTimer);
            },
            getResults(page = 1) {
                paymentService.getAllPayments(page, (this.$props.searchable !== undefined ? this.$props.searchable : '')).then((response) => {
                    this.laravelData = response.data;
                    this.total_rows = response.data.total;
                }).catch((error) => {
                    window.EventBus.$emit('error', {
                        message: this.$showError(error),
                    });
                });
            }
        }
    }
</script>
