<style scoped>
    .has-success:after, .has-danger:after {
        top: 33px;
    }
</style>
<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
            <!-- Card stats -->
            <div class="row">
                <div class="col-xl-12 col-lg-12"  v-if="$can('payments.download')">
                    <stats-card title="Download Report"
                                type="gradient-blue"
                                icon="ni ni-archive-2"
                                class="mb-4 mb-xl-0"
                    >
                        <template slot="footer">
                            <form>
                                <div class="row">
                                    <div class="col-lg-4">
                                        <!--<base-input alternative=""
                                                    label="Start Date"
                                                    placeholder="Start Date"
                                                    input-classes="form-control-alternative"
                                                    v-model="report.startDate"
                                        />
                                        <datepicker v-model="report.startDate" name="uniquename"></datepicker>-->
                                        <label> Start Date</label>
                                        <datepicker :disabled-dates="disabledDates" :bootstrap-styling="true" :use-utc="true"  v-model="report.startDate">
                                        </datepicker>
                                    </div>
                                    <div class="col-lg-4">
                                        <label> End Date</label>
                                        <datepicker :disabled-dates="disabledDates" :bootstrap-styling="true" :use-utc="true"  v-model="report.endDate">
                                        </datepicker>
                                    </div>
                                    <div class="col-lg-4" style="padding-top: 5px">
                                        <base-button class="mt-4" native-type="button" v-on:click="downloadCSV">Download in CSV</base-button>
                                    </div>
                                </div>
                            </form>
                        </template>
                    </stats-card>
                </div>
            </div>
        </base-header>

        <!--Form-->
        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col-lg-12">
                    <all-payments-table :permission-prefix="permissionPrefix"></all-payments-table>
                </div>
            </div>
            <!-- End From-->
        </div>

    </div>
</template>
<script>
    import {required, minLength, maxLength} from 'vuelidate/lib/validators';
    import paymentService from '../store/modules/payment';
    import AllPaymentsTable from "./Tables/AllPaymentsTable";
    import Datepicker from 'vuejs-datepicker';
    import helpers from "../utils/helpers";


    export default {
        components: {AllPaymentsTable, Datepicker},
        mixins: [helpers],
        props: {
        },
        data() {
            return {
                searchable: '01770618575',
                submitStatus: '',
                error: '',
                payments: [],
                report: {
                    startDate: "",
                    endDate: ''
                },

                disabledDates: {
                    to: this.threeMonthOldDate(), // Disable all dates up to specific date
                    from: new Date(), // Disable all dates after specific date
                },
                permissionPrefix: 'payments'
            };
        },
        validations: {
            searchable: {
                required,
                minLength: minLength(10),
                maxLength: maxLength(11)
            }
        },
        methods: {
            submit() {
                this.$v.$touch();
                if (this.$v.$invalid) {
                    this.submitStatus = 'ERROR';
                } else {
                    // do your submit logic here
                    this.submitStatus = 'PENDING';
                    window.console.log(this.$v.searchable);
                    paymentService.searchPayment(this.$v.searchable.$model).then((resp) => {
                        this.submitStatus = 'OK';
                        this.payments = resp.data;
                    }).catch((error) => {
                        this.submitStatus = 'ERROR';
                        window.EventBus.$emit('error', {
                            message: this.$showError(error),
                        });
                    });
                }
            },
            downloadCSV() {
                if(this.report.startDate === '' || this.report.startDate == null){
                    window.EventBus.$emit('error', {
                        message: 'Please provide valid start date',
                    });
                } else if (this.report.endDate === '' || this.report.endDate == null) {
                    window.EventBus.$emit('error', {
                        message: 'Please provide valid end date',
                    });
                } else {
                    window.console.log(this.report.startDate, this.report.endDate);
                    paymentService.downloadCSVReport(this.report).then((resp) => {
                        let blobData = new Blob([resp.data]);
                        var fileURL = window.URL.createObjectURL(blobData);
                        var fileLink = document.createElement('a');

                        fileLink.href = fileURL;
                        fileLink.setAttribute('download', 'report.xlsx');
                        document.body.appendChild(fileLink);

                        fileLink.click();
                        window.console.log(resp.data);
                    }).catch((error) => {
                        window.EventBus.$emit('error', {
                            message: this.$showError(error),
                        });
                    });
                }
            },
            threeMonthOldDate() {
                let date = new Date();
                date.setMonth(date.getMonth() - 3);
                return date;
            }
        }
    };

</script>
<style></style>
