<style scoped>
    .has-success:after, .has-danger:after {
        top: 33px;
    }
</style>
<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
        </base-header>

        <!--Form-->
        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col-lg-12">
                    <ListingTable :create-or-edit-form="createOrEditForm" :title="'All Stores'"
                                  :table-headers="tableHeaders" :api-method="apiMethod"
                                  :service="service" :permission-prefix="permissionPrefix"
                                  :search-criteria="'By name'"></ListingTable>
                </div>
            </div>
            <!-- End From-->
        </div>

    </div>
</template>
<script>
    import {required, minLength, maxLength} from 'vuelidate/lib/validators';
    import adminService from '../../../src/store/modules/admin';
    import ListingTable from "../Common/Tables/ListingTable";


    export default {
        components: {ListingTable},
        props: {},
        data() {
            return {
                searchable: '01770618575',
                submitStatus: '',
                error: '',
                payments: [],
                report: {
                    startDate: "",
                    endDate: ''
                },
                tableHeaders: {
                    'id': 'ID',
                    'name': 'Name',
                    'address' : 'Address',
                    'mobile_no': 'Phone Number',
                    'status' : 'Status',
                    'created_at': 'Creation Time',
                    'updated_at': 'Updation Time',
                },
                createOrEditForm: [
                    {
                        id: 1,
                        type: 'text',
                        label: 'Store Name',
                        key: 'name'
                    },
                    {
                        id: 2,
                        type: 'text',
                        label: 'Address',
                        key: 'address'
                    },
                    {
                        id: 3,
                        type: 'text',
                        nativeType: 'number',
                        label: 'Store Phone Number',
                        key: 'mobile_no',
                        // disableUpdate: true
                    },
                    {
                        id: 4,
                        type: 'select',
                        label: 'Status',
                        key: 'status',
                        options: {
                            ACTIVE: "ACTIVE",
                            INACTIVE: "INACTIVE"
                        }
                    }
                ],
                apiMethod: {list: 'getAllStores', entry: 'createOrUpdateStore', delete: 'deleteStore'},
                service: adminService,
                permissionPrefix: 'stores'
            };
        },
        validations: {
            searchable: {
                required,
                minLength: minLength(10),
                maxLength: maxLength(11)
            }
        },
        methods: {

        }
    };

</script>
