<style scoped>
    .has-success:after, .has-danger:after {
        top: 33px;
    }
</style>
<template>
    <div>

        <!-- HEADER -->
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8" v-if="$can('payments.search')">
            <!-- Card stats -->
            <div class="row">
                <div class="col-xl-12 col-lg-12">
                    <stats-card title="Search Payment"
                                type="gradient-green"
                                icon="ni ni-money-coins"
                                class="mb-4 mb-xl-0"
                    >

                        <template slot="footer">
                            <form action="#" @submit.prevent="submit">
                                <div class="row">
                                    <div class="col-lg-6">
                                        <base-input
                                                :valid="!$v.searchable.$dirty ? null : (error === '' ? true : false )"
                                                :error="error" alternative=""
                                                label="bKash account number / Transaction ID"
                                                placeholder="Phone / ID"
                                                input-classes="form-control-alternative"
                                                v-on:keyup="validateInput()"
                                                v-model.trim="$v.searchable.$model"
                                        ></base-input>
                                    </div>
                                    <div class="col-lg-6" style="padding-top: 31px;">
                                        <base-button native-type="submit" :disabled="submitStatus === 'PENDING'"
                                                     class="btn btn-dark">Search
                                        </base-button>
                                    </div>
                                </div>
                            </form>
                        </template>
                    </stats-card>
                </div>
            </div>
        </base-header>

        <!--List of Payments-->
        <div class="container-fluid mt--7" v-if="$can('payments.search')">
            <div class="row">

                <div class="col-xl-6 mb-4" v-for="payment in payments" :key="payment.trx_id">
                    <card shadow type="secondary">
                        <div slot="header" class="bg-white border-0">
                            <div class="row align-items-center">
                                <div class="col-8">
                                    <h3 class="mb-0">Payment - {{ payment.trx_id}}</h3>
                                </div>
                                <div class="col-4 text-right">
                                    <!--                                    <a href="#!" class="btn btn-sm btn-primary">Settings</a>-->
                                </div>
                            </div>
                        </div>
                        <template>
                            <form @submit.prevent>
                                <h6 class="heading-small text-muted mb-4">Payment information</h6>
                                <div class="row">
                                    <div class="col-lg-12 table-responsive">
                                        <table class="table">
                                            <tr>
                                                <td><b>Sender</b></td>
                                                <td>{{ payment.sender_account_no }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Amount</b></td>
                                                <td>{{ payment.amount }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Currency</b></td>
                                                <td>{{ payment.currency }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Transaction Type</b></td>
                                                <td>{{ payment.transactionType }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Merchant</b></td>
                                                <td>{{ payment.merchant ? payment.merchant.name + " (" + payment.merchant.account_no + ")": 'N/A' }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Merchant Reference</b></td>
                                                <td>{{ payment.merchant_ref }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>User Reference</b></td>
                                                <td>{{ payment.transactionReference }}</td>
                                            </tr>
                                            <tr>
                                                <td><b>Paid At</b></td>
                                                <td>{{ payment.transaction_datetime }}</td>
                                            </tr>
                                        </table>
                                    </div>
                                </div>
                                <hr class="my-4"/>
                                <!-- Address -->
                                <h6 class="heading-small text-muted mb-4">Actions</h6>
                                <div class="row" v-if="payment.merchant_ref === null && $can('payments.update')">
                                    <div class="col-md-12">
                                        <base-button v-on:click="addReference(payment)" type="warning" size="sm"
                                                     icon="ni ni-fat-add">Add reference
                                        </base-button>
                                    </div>
                                </div>
                            </form>
                        </template>
                    </card>
                </div>
            </div>
            <!-- End List of Payments-->
        </div>


        <!-- MODAL -->
        <modal :type="''" :show.sync="modals.modal1" v-if="$can('payments.update')"
               body-classes="p-0"
               modal-classes="modal-dialog-centered modal-sm">
            <card type="secondary" shadow
                  header-classes="bg-white pb-5"
                  body-classes="px-lg-5 py-lg-5"
                  class="border-0">
                <template>
                    <form role="form" @submit.prevent="update">

                        <div>
                            <base-input required="required" label="Reference" class="mb-3"
                                        v-model="reference"
                                        placeholder="Reference"></base-input>
                        </div>
                        <div class="text-center">
                            <base-button native-type="submit" type="primary" class="my-4">
                                Update
                            </base-button>
                        </div>
                    </form>
                </template>
            </card>
        </modal>
        <!-- END OF MODAL -->

    </div>
</template>
<script>
    import {required, minLength, maxLength} from 'vuelidate/lib/validators';
    import paymentService from '../store/modules/payment';
    import helpers from "../utils/helpers";

    export default {
        components: {},
        mixins: [helpers],
        data() {
            return {
                searchable: '',
                submitStatus: '',
                error: '',
                payments: [],
                modals: {
                    modal1: false
                },
                reference: '',
                selectedPayment: {}
            };
        },
        validations: {
            searchable: {
                required,
                minLength: minLength(10),
                maxLength: maxLength(11)
            }
        },
        methods: {
            submit() {
                this.$v.$touch();
                if (this.$v.$invalid) {
                    this.submitStatus = 'ERROR';
                } else {
                    // do your submit logic here
                    this.payments = [];
                    this.submitStatus = 'PENDING';
                    window.console.log(this.$v.searchable);
                    paymentService.searchPayment(this.$v.searchable.$model).then((resp) => {
                        this.submitStatus = 'OK';
                        this.payments = resp.data;
                    }).catch((error) => {
                        this.submitStatus = 'ERROR';
                        window.EventBus.$emit('error', {
                            message: this.$showError(error),
                        });
                    });
                }
            },
            validateInput() {
                this.error = '';
                if (!this.$v.searchable.required) {
                    this.error = 'Please input valid account number or transaction ID';
                } else if (!this.$v.searchable.minLength) {
                    this.error = 'Input must be minimum of ' + this.$v.searchable.$params.minLength.min + ' length';
                } else if (!this.$v.searchable.maxLength) {
                    this.error = 'Input must be maximum of ' + this.$v.searchable.$params.maxLength.max + ' length';
                }
            },
            addReference(payment) {
                this.modals.modal1 = true;
                this.selectedPayment = payment;
            },
            update() {
                window.console.log('Update');
                if(this.reference === undefined || this.reference === '' || this.reference == null) {
                    window.EventBus.$emit('error', {
                       message: 'Please provide a reference'
                    });
                } else {
                    if(this.selectedPayment.id) {
                        paymentService.updatePayment(this.selectedPayment.id, {'reference': this.reference}).then(() => {
                            this.modals.modal1 = false;
                            this.selectedPayment.merchant_ref = this.reference;
                            window.EventBus.$emit('success', {
                                message: 'Information has been updated successfully',
                            });
                        }).catch((error) => {
                            this.submitStatus = 'ERROR';
                            window.EventBus.$emit('error', {
                                message: this.$showError(error),
                            });
                        });
                    }
                }
            }
        }
    };
</script>
<style></style>
