<template>
    <div class="row justify-content-center">
        <div class="col-lg-5 col-md-7">
            <div class="card bg-secondary shadow border-0">
                <div class="card-body px-lg-5 py-lg-5">
                    <div class="text-center text-muted mb-4">
                        <small>Sign in with credentials</small>
                    </div>
                    <form role="form" @submit.prevent="login">
                        <base-input class="input-group-alternative mb-3"
                                    placeholder="Username"
                                    addon-left-icon="ni ni-single-02"
                                    v-model="username">
                        </base-input>

                        <base-input class="input-group-alternative"
                                    placeholder="Password"
                                    type="password"
                                    addon-left-icon="ni ni-lock-circle-open"
                                    v-model="password">
                        </base-input>

                        <base-checkbox class="custom-control-alternative" v-model="remember">
                            <span class="text-muted">Remember me</span>
                        </base-checkbox>
                        <div class="text-center">
                            <base-button type="primary" v-on:click="login" class="my-4">Sign in</base-button>
                        </div>
                    </form>
                </div>
            </div>
            <div class="row mt-3">
                <!-- <div class="col-6">
                    <a href="#" class="text-light">
                        <small>Forgot password?</small>
                    </a>
                </div> -->
                <!--<div class="col-6 text-right">
                    <router-link to="/register" class="text-light">
                        <small>Create new account</small>
                    </router-link>
                </div>-->
            </div>
        </div>
    </div>
</template>
<script>
    import { AUTH_REQUEST } from "../store/actions/auth";
    import helpers from "../utils/helpers";
    import Vue from 'vue';

    export default {
        name: 'login',
        mixins: [helpers],
        data() {
            return {
                username: 'admin',
                password: '123456',
                remember: false
            }
        },
        methods: {
            login: function () {
                if(this.username === '' || this.password === '') {
                    Vue.notify("Please provide valid information");
                    window.EventBus.$emit('danger', {
                        message: 'Please provide valid information',
                    });
                } else {
                    const {username, password, remember} = this;
                    this.$store.dispatch(AUTH_REQUEST, {username, password, remember}).then(() => {
                        this.$router.push('/dashboard', () => {
                            window.EventBus.$emit('success', {
                                message: 'Welcome back, logged in successfully',
                            });
                        })
                    }, (error) => {
                        Vue.notify(this.$showError(error));
                        // window.EventBus.$emit('danger', {
                        //     message: 'Error with Login, please check your credentials',
                        // });
                        // window.EventBus.$emit('error', {
                        //     message: this.$showError(error),
                        // });
                    })
                }
            }
        }
    }
</script>
