                                    {{ profile.name }}
                                    <span class="font-weight-light">, <small>({{ profile.username }})</small></span>
                                </h3>
                                <div class="h4 font-weight-300" v-if="profile.myroles && profile.myroles.length > 0">
                                    <i class="ni location_pin mr-2"></i>Role :
                                    <badge v-for="r in profile.myroles" :key="r" type="default">{{ r }}</badge>
                                </div>
