                                                            placeholder="Confirm Password"
                                                            input-classes="form-control-alternative"
                                                            v-model="model.new_password_confirmation"
                                                />
                                            </div>
                                        </div>
                                        <div class="row">
                                            <div class="col-md-12">
                                                <base-button native-type="password" type="warning">
