</template>
<script>
    import adminServices from '../../store/modules/admin';
    import helpers from "../../utils/helpers";

    export default {
        name: 'user-profile',
        mixins: [helpers],
        data() {
            return {
                profile: {},
                model: {
                    'old_password': '',
                    'new_password': '',
                    'new_password_confirmation': ''
                }
            }
        },
        methods: {
            loadProfile() {
                adminServices.getCurrentAdmins().then((response) => {
                    this.profile = response.data;
                }).catch((error) => {
                    window.EventBus.$emit('error', {
                        message: this.$showError(error),
                    });
                });
            },
            changePassword() {
                if (!this.model.old_password || !this.model.new_password || !this.model.new_password_confirmation) {
                    window.EventBus.$emit('error', {
                        message: 'Please provide valid old password, new password, and confirm the password'
                    });
                } else {
                    if (this.model.new_password !== this.model.new_password_confirmation) {
                        window.EventBus.$emit('error', {
                            message: 'Password is mismatching with confirm password'
                        });
                    } else {
                        adminServices.changeAdminPassword(this.model).then(() => {
                            window.EventBus.$emit('success', {
                                message: 'Password has been changed successfully'
                            });
                        }, (error) => {
                            window.EventBus.$emit('error', {
                                message: this.$showError(error)
                            });
                        })
                    }
                }
            }
        },
        mounted() {
            this.loadProfile();
        }
    };
</script>
