<style scoped>
    .has-success:after, .has-danger:after {
        top: 33px;
    }
</style>
<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
        </base-header>

        <!--Form-->
        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col-lg-12">
                    <ListingTable :create-or-edit-form="createOrEditForm" :title="'Products'"
                                  :table-headers="tableHeaders" :api-method="apiMethod"
                                  :service="service" :permission-prefix="permissionPrefix"
                                  :search-criteria="'By title'"></ListingTable>
                </div>
            </div>
            <!-- End From-->
        </div>

    </div>
</template>
<script>
    import {required, minLength, maxLength} from 'vuelidate/lib/validators';
    import paymentService from '../../../src/store/modules/payment';
    import adminService from '../../../src/store/modules/admin';
    import ListingTable from "../Common/Tables/ListingTable";


    export default {
        components: {ListingTable},
        props: {},
        data() {
            return {
                searchable: '01770618575',
                submitStatus: '',
                error: '',
                payments: [],
                report: {
                    startDate: "",
                    endDate: ''
                },

                disabledDates: {
                    to: this.threeMonthOldDate(), // Disable all dates up to specific date
                    from: new Date(), // Disable all dates after specific date
                },
                tableHeaders: {
                    'id': 'ID',
                    'title': 'Title',
                    'brand': 'Brand Name',
                    'unit': 'Unit',
                    'created_at': 'Creation Time',
                    'updated_at': 'Updation Time',
                },
                createOrEditForm: [
                    {
                        id: 1,
                        type: 'text',
                        label: 'Title',
                        key: 'title'
                    },
                    {
                        id: 2,
                        type: 'text',
                        label: 'Brand',
                        key: 'brand'
                    },
                    {
                        id: 3,
                        type: 'text',
                        label: 'Description',
                        key: 'description'
                    },
                    {
                        id: 4,
                        type: 'select-single',
                        label: 'Unit',
                        key: 'unit',
                        options: [],
                        apiMethod: "getUnitOfProducts"
                    }
                ],
                apiMethod: {list: 'getAllProducts', entry: 'createOrUpdateProduct', delete: 'deleteProduct'},
                service: adminService,
                permissionPrefix: 'products'
            };
        },
        validations: {
            searchable: {
                required,
                minLength: minLength(10),
                maxLength: maxLength(11)
            }
        },
        methods: {
            submit() {
                this.$v.$touch();
                if (this.$v.$invalid) {
                    this.submitStatus = 'ERROR';
                } else {
                    // do your submit logic here
                    this.submitStatus = 'PENDING';
                    window.console.log(this.$v.searchable);
                    paymentService.searchPayment(this.$v.searchable.$model).then((resp) => {
                        this.submitStatus = 'OK';
                        this.payments = resp.data;
                    }).catch((error) => {
                        this.submitStatus = 'ERROR';
                        window.EventBus.$emit('error', {
                            message: 'No payment information found, trace: ' + error,
                        });
                    });
                }
            },
            downloadCSV() {
                if (this.report.startDate === '' || this.report.startDate == null) {
                    window.EventBus.$emit('error', {
                        message: 'Please provide valid start date',
                    });
                } else if (this.report.endDate === '' || this.report.endDate == null) {
                    window.EventBus.$emit('error', {
                        message: 'Please provide valid end date',
                    });
                } else {
                    window.console.log(this.report.startDate, this.report.endDate);
                    paymentService.downloadCSVReport(this.report).then((resp) => {
                        let blobData = new Blob([resp.data]);
                        var fileURL = window.URL.createObjectURL(blobData);
                        var fileLink = document.createElement('a');

                        fileLink.href = fileURL;
                        fileLink.setAttribute('download', 'report.xlsx');
                        document.body.appendChild(fileLink);

                        fileLink.click();
                        window.console.log(resp.data);
                    }).catch((error) => {
                        window.EventBus.$emit('error', {
                            message: 'Download request cannot be served now, trace: ' + error,
                        });
                    });
                }
            },
            threeMonthOldDate() {
                let date = new Date();
                date.setMonth(date.getMonth() - 3);
                return date;
            }
        }
    };

</script>
<style></style>
