import URL from "../../utils/url";
import apiCall from "../../utils/api";

const paymentService = {
    searchPayment(reference) {
        return apiCall({url: URL.baseURL + URL.paymentSearchURL + reference, method: "GET"});
    },
    updatePayment(id, data) {
        return apiCall({url: URL.baseURL + URL.paymentUpdateURL + id, data: data, method: "PUT"});
    },
    missedPayment(reference) {
        return apiCall({url: URL.baseURL + URL.paymentMissedURL + reference, method: "GET"});
    },
    getAllPayments(page, searchable) {
        return apiCall({url: URL.baseURL + URL.getPaymentsURL + '?page=' + page + '&search='+searchable, method: "GET"});
    },
    downloadCSVReport(report){
        return apiCall({url: URL.baseURL + URL.downloadReportURL, data: {'start' : report.startDate, 'end': report.endDate }, method: "POST", responseType: 'blob'});
    }
};

export default paymentService;