<style scoped>
    .has-success:after, .has-danger:after {
        top: 33px;
    }
</style>
<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
        </base-header>

        <!--Form-->
        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col-lg-12">
                    <ListingTable :create-or-edit-form="createOrEditForm" :title="'All Roles'"
                                  :table-headers="tableHeaders" :api-method="apiMethod"
                                  :service="service" :permission-prefix="permissionPrefix"
                                  :search-criteria="'By name'"></ListingTable>
                </div>
            </div>
            <!-- End From-->
        </div>

    </div>
</template>
<script>
    import {required, minLength, maxLength} from 'vuelidate/lib/validators';
    import adminService from '../../../src/store/modules/admin';
    import ListingTable from "../Common/Tables/ListingTable";


    export default {
        components: {ListingTable},
        props: {},
        data() {
            return {
                searchable: '01770618575',
                submitStatus: '',
                error: '',
                payments: [],
                tableHeaders: {
                    'id': 'ID',
                    'name': 'Name',
                    'guard_name': 'Role For',
                    'permissions' : 'Permissions',
                    'created_at': 'Creation Time',
                    'updated_at': 'Updation Time',
                },
                createOrEditForm: [
                    {
                        id: 1,
                        type: 'text',
                        label: 'Role Name',
                        key: 'name'
                    },
                    {
                        id: 2,
                        type: 'select',
                        label: 'Role for',
                        key: 'guard_name',
                        options: {
                            admin: "Admins",
                            user: "Clients"
                        },
                        onChangeOptions: "getOptions",
                        dependent: 3,
                        disableUpdate: true
                    },
                    {
                        id: 3,
                        type: 'select-multiple',
                        label: 'Permissions',
                        key: 'permissions',
                        options: [],
                        apiMethod: "getRolePermissions",
                        dependOn: 2
                    }
                ],
                apiMethod: {list: 'getAllRoles', entry: 'createOrUpdateRole', delete: 'deleteRole'},
                service: adminService,
                permissionPrefix: 'roles',
            };
        },
        validations: {
            searchable: {
                required,
                minLength: minLength(10),
                maxLength: maxLength(11)
            }
        },
        methods: {
        }
    };

</script>
<style></style>
